import { connect } from 'react-redux';

import { getPostDetail, fetchPostDetail } from '../../actions';
import { PostConfigView } from './view';

const mapStateToProps = (state) => {
    return {
        postDetail: state.postDetail,
        loadingPostDetail: state.loadingPostDetail
    };
};

const mapDispatchToProps = {
    getPostDetail,
    fetchPostDetail
};

export const PostConfigComponent = connect(mapStateToProps, mapDispatchToProps)(PostConfigView);
