import React, { useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { addDoc, collection, setDoc, doc } from 'firebase/firestore';

import { db } from '../../firebase-config';
import { MarkdownEditor, EditorTextInput } from '../../components';

export const PostConfigView = ({ fetchPostDetail, loadingPostDetail, postDetail }) => {
    const navigate = useNavigate();
    const { uuid = '' } = useParams();

    useEffect(() => {
        const userAccessToken = sessionStorage.getItem('accessToken');
        const uid = sessionStorage.removeItem('uid');
        const email = sessionStorage.removeItem('email');
        if (!userAccessToken && !uid && !email) navigate('/login');
        if (uuid) fetchPostDetail(uuid);
    }, []);

    const handleOnSubmit = async (evt) => {
        evt.preventDefault();
        const formData = new FormData(evt.target);
        const editorValue = formData.get('editor');
        const textValue = formData.get('title');

        if (editorValue && textValue) {
            uuid ? await setDoc(doc(db, 'blog-data', String(uuid)), {
                title: textValue,
                description: editorValue
            }).then(() => {
                navigate('/admin-space/list-post');
            }).catch((error) => {
                alert(error);
            }) : await addDoc(collection(db, 'blog-data'), {
                title: textValue,
                description: editorValue
            }).then(() => {
                navigate('/admin-space/list-post');
            }).catch((error) => {
                alert(error);
            });
        } else {
            alert('Both fields are required');
        }
    };

    const RenderForm = () => {
        return (
            <form className='w-100' onSubmit={handleOnSubmit}>
                <div className='save-btn-wrapper'>
                    <button type='submit' className='login-btn'>
                        SAVE
                    </button>
                </div>
                <div>
                    <EditorTextInput value={uuid ? postDetail.title : ''} />
                </div>
                <div>
                    <label htmlFor='blog-description'>Description</label>
                    <br />
                    <MarkdownEditor value={uuid ? postDetail.description : ''} />
                </div>
            </form>
        );
    };

    return (
        <div className='editor-container'>
            {loadingPostDetail ? <div className='loader'></div> : <RenderForm />}
        </div>
    );
};
