import React, { useEffect } from 'react';
import { useNavigate, Outlet } from 'react-router-dom';

import { Header, HeaderButton, HeaderLink } from '../components/header';
import { logout } from '../firebase-config';

export const AdminSpace = () => {
    const navigate = useNavigate();

    useEffect(() => {
        const userAccessToken = sessionStorage.getItem('accessToken');
        const uid = sessionStorage.removeItem('uid');
        const email = sessionStorage.removeItem('email');
        if (!userAccessToken && !uid && !email) {
            navigate('/login');
        }
    }, []);

    const userLogout = async () => {
        await logout();
        const userAccessToken = sessionStorage.getItem('accessToken');
        if (!userAccessToken) {
            navigate('/login');
        }
    };


    return (
        <>
            <Header actions={(
                <>
                    <HeaderLink to={'/'}>Home</HeaderLink>
                    <HeaderLink to={'/admin-space/create'}>Create</HeaderLink>
                    <HeaderLink to={'/admin-space/list-post'}>List</HeaderLink>
                    <HeaderButton isLogout={true} onClick={userLogout}>Logout</HeaderButton>
                </>
            )}/>
            <Outlet />
        </>
    );
};
