import { useEffect, useState } from 'react';
import ReactMarkdown from 'react-markdown';
import { useParams } from 'react-router-dom';
import { doc, getDoc } from 'firebase/firestore';

import { db } from '../../firebase-config';
import { Header, HeaderLink } from '../../components/header';

export const BlogDetailContainer = ({ postDetail, getPostDetail }) => {
    const { uuid = '' } = useParams();
    const [isloading, setIsloading] = useState(false);

    const getData = async () => {
        if (uuid && Object.keys(postDetail).length === 0) {
            setIsloading(true);
            const querySnapshot = await getDoc(doc(db, 'blog-data', String(uuid)));
            getPostDetail(querySnapshot.data());
            setIsloading(false);
        }
    };

    useEffect(() => {
        getData();
    }, []);

    return (
        <>
            <Header actions={<HeaderLink to={'/'}>HOME</HeaderLink>}/>
            <div className='landing-container'>
                {isloading ? <div className='loader'></div> : (
                    <>
                        <div className='post-title'>{postDetail?.title}</div>
                        <div className='post-description-container'>
                            <ReactMarkdown>
                                {postDetail?.description}
                            </ReactMarkdown>
                        </div>
                    </>
                )}
            </div>
        </>
    );
};
