import { connect } from 'react-redux';

import { getPostList, getPostDetail } from '../../actions';
import { LandingPageContainer } from './view';

const mapStateToProps = (state) => {
    return {
        postList: state.postList
    };
};

const mapDispatchToProps = {
    getPostList,
    getPostDetail
};

export const LandingPage = connect(mapStateToProps, mapDispatchToProps)(LandingPageContainer);
