import { HashRouter, Route, Routes, Navigate } from 'react-router-dom';

// Components
import { Login, AdminSpace, PostConfigComponent, PostList } from './admin-space';
import { LandingPage, BlogDetail } from './blog-ui';

// CSS
import './app.css';

const App = () => {
    return (
        <HashRouter>
            <Routes>
                <Route path='/admin-space' element={<Navigate to='/admin-space/list-post' />} />
                <Route element={<AdminSpace />}>
                    <Route path='admin-space/create' element={<PostConfigComponent />}/>
                    <Route path='admin-space/:uuid/edit' element={<PostConfigComponent />}/>
                    <Route path='admin-space/list-post' element={<PostList />}/>
                </Route>
                <Route path='login' element={<Login />} />
                <Route path='detail/:uuid' element={<BlogDetail />} />
                <Route path='/' element={<LandingPage />} />
            </Routes>
        </HashRouter>
    );
};

export default App;
