import React, { useState, useEffect } from 'react';
import { collection, getDocs } from 'firebase/firestore';

import { Header } from '../../components/header';
import { db } from '../../firebase-config';
import { Card } from '../../components/card';

export const LandingPageContainer = ({ postList, getPostList, getPostDetail }) => {
    const [isloading, setIsloading] = useState(false);

    const getData = async () => {
        const titlesData = [];
        if (postList.length === 0) {
            setIsloading(true);
            const querySnapshot = await getDocs(collection(db, 'blog-data'));
            querySnapshot.docs.forEach((blog) => {
                if (blog.data()) {
                    titlesData.push({ ...blog.data(), uuid: blog.id });
                }
            });
            getPostList(titlesData);
            setIsloading(false);
        }
    };

    useEffect(() => {
        getData();
    }, []);

    const RenderCard = () => postList.map((post, index) => (
        <Card
            key={index}
            post={post}
            getPostDetail={getPostDetail} />
    ));

    return (
        <>
            <Header />
            <div className='landing-container'>
                {isloading ? <div className='loader'></div> : <RenderCard />}
            </div>
        </>
    );
};
