import { initializeApp } from 'firebase/app';
import {
    GoogleAuthProvider,
    getAuth,
    signInWithPopup,
    signInWithEmailAndPassword,
    createUserWithEmailAndPassword,
    sendPasswordResetEmail,
    signOut
} from 'firebase/auth';
import {
    getFirestore,
    query,
    getDocs,
    collection,
    where,
    addDoc
} from 'firebase/firestore';


const firebaseConfig = {
    apiKey: 'AIzaSyCTeJ4KJTUbu7d_IzFSWmL3IsFrejtkYO0',
    authDomain: 'portfolio-blog-267a4.firebaseapp.com',
    projectId: 'portfolio-blog-267a4',
    storageBucket: 'portfolio-blog-267a4.appspot.com',
    messagingSenderId: '275749680293',
    appId: '1:275749680293:web:749f480760d58bd6be894c',
    measurementId: 'G-52H54X43X7'
};
const app = initializeApp(firebaseConfig);
const auth = getAuth(app);
const db = getFirestore(app);
const googleProvider = new GoogleAuthProvider();

const signInWithGoogle = async () => {
    try {
        const res = await signInWithPopup(auth, googleProvider);
        const user = res.user;
        const q = query(collection(db, 'users'), where('uid', '==', user.uid));
        const docs = await getDocs(q);
        if (docs.docs.length === 0) {
            await addDoc(collection(db, 'users'), {
                uid: user.uid,
                name: user.displayName,
                authProvider: 'google',
                email: user.email
            });
        }
    } catch (err) {
        console.error(err);
        alert(err.message);
    }
};

const logInWithEmailAndPassword = async (email, password, navigate) => {
    try {
        await signInWithEmailAndPassword(auth, email, password).then((userCredential) => {
            sessionStorage.setItem('accessToken', userCredential.user.accessToken);
            sessionStorage.setItem('uid', userCredential.user.uid);
            sessionStorage.setItem('email', userCredential.user.email);
            navigate('/admin-space', { replace: true });
        })
            .catch((error) => {
                const errorCode = error.code;
                const errorMessage = error.message;
                alert(`${errorCode} - ${errorMessage}`);
            });
    } catch (err) {
        console.error(err);
        alert(err.message);
    }
};

const registerWithEmailAndPassword = async (name, email, password) => {
    try {
        const res = await createUserWithEmailAndPassword(auth, email, password);
        const user = res.user;
        await addDoc(collection(db, 'users'), {
            uid: user.uid,
            name,
            authProvider: 'local',
            email
        });
    } catch (err) {
        console.error(err);
        alert(err.message);
    }
};

const sendPasswordReset = async (email) => {
    try {
        await sendPasswordResetEmail(auth, email);
        alert('Password reset link sent!');
    } catch (err) {
        console.error(err);
        alert(err.message);
    }
};

const logout = () => {
    sessionStorage.removeItem('accessToken');
    sessionStorage.removeItem('uid');
    sessionStorage.removeItem('email');
    signOut(auth);
};


export {
    auth,
    db,
    signInWithGoogle,
    logInWithEmailAndPassword,
    registerWithEmailAndPassword,
    sendPasswordReset,
    logout
};
